import React, {
  useState,
  ReactNode,
  useEffect,
  FC
} from 'react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl';
  style?: React.CSSProperties;
}

const Button: FC<ButtonProps> = ({
  children,
  size = 'sm',
  style,
  onClick
}: ButtonProps) => {

  const [sizeExists, setSizeExists] = useState(false);

  useEffect(() => {
    size
      ? setSizeExists(true)
      : setSizeExists(false);
  }, [size]);

  const sizeValue =
    size === 'xs'
      ? {
        height: 30
        }
      : size === 'sm'
        ? {
          height: 40
          }
        : size === 'md'
          ? {
            height: 45
            }
          : size === 'lg'
            ? {
              height: 50
              }
            : {
              height: 55
              };

  const defaultStyles = {
    border: '1px solid #ccc',
    padding: '5px 10px',
    textAlign: 'center',
    wrap: 'nowrap',
    width: 'auto',
    ...sizeValue,
    ...style,
    } as React.CSSProperties;

  return (
    <button
      onClick={onClick}
      style={
        style
          ? style
          : defaultStyles
      }
    >
      {children}
    </button>
  )
}

export default Button;
